@import url(https://fonts.googleapis.com/css2?family=KoHo:ital,wght@1,700&display=swap);
html,
body {
  text-align: center;
  margin: 0;
  height: 100%;
}

/* ancher */
.ac-deco {
  text-decoration: none;
  color: #aaaa;
}

.ac-deco:link {
  text-decoration: none;
}

.ac-deco:visited {
  text-decoration: none;
}
.ac-deco:-webkit-any-link {
  text-decoration: none;
}

body {
  box-sizing: border-box;
  padding-top: 71px;
}
/* rootdiv */
#rootDiv {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}
/* header */

header {
  float: left;
  box-sizing: border-box;
  background-color: white;
  height: 71px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  /* border-bottom: 1px solid #dbdbdb; */
  position: fixed;
  top: 0;
  width: 100vw;
  z-index: 100;
}

.header_div {
  width: 1440px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}
.navbar {
  box-sizing: border-box;
  display: -webkit-flex;
  display: flex;
  width: 100%;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  padding: 0 20px;
}
.nav_rogoimg {
  height: 100%;
  -webkit-align-self: flex-start;
          align-self: flex-start;
  margin: 0 20px;
}

.nav_rogoimg img {
  height: 100%;
  object-fit: cover;
}

.header_ul {
  display: -webkit-flex;
  display: flex;
  list-style: none;
  padding: 0 50px;
  -webkit-justify-content: center;
          justify-content: center;
}

.header_ul_li {
  float: left;
  margin-left: 11px;
  color: #aaaa;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 3px;
  font-family: "Poppins", Arial, sans-serif;
  padding: 5px;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out,
    border-color 0.15s ease-in-out;
  line-height: 1.8px;
}

.header_ul :nth-child(1) a {
  color: black;
}

.header_ul_li a:link {
  color: #aaaa;
}

.header_ul :nth-child(1) a:link {
  color: #aaaa;
}

.header_ul .header_ul_li a:visited {
  color: #aaaa;
}

.header_ul :nth-child(1) a:visited {
  color: black;
}

.header_ul_li a:hover {
  color: #a17672;
}

/* body section */
section.body {
  box-sizing: border-box;
}

/* footer */
footer {
  background-color: #f4f4f4;
}

/* main - footer - content */
.Footer-wrapper {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-flex-direction: column;
          flex-direction: column;
  height: 100px;
  padding-top: 30px;
  color: rgba(63, 63, 63, 0.667);
}

.f-w-contact {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.f-w-c-span {
  margin: 0 40px;
}

.f-w-c-span span a {
  color: rgba(63, 63, 63, 0.667);
}

.f-w-create {
  text-align: left;
  padding: 0 0 10px 20px;
  font-size: 13px;
}

/* fonts */

.MainIntro-Section {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}
.MainIntro-wrapper {
  width: 100%;
  height: 50vh;
  background-color: #f4f4f4;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  z-index: 0;
  position: fixed;
  top: 71px;
}

.intro-inner-wrapper {
  box-sizing: border-box;
}

.iiw-f1,
.iiw-f2 {
  margin: 0;
}

.iiw-f1,
.iiw-f3 {
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s;
  -webkit-animation-name: introtext;
          animation-name: introtext;
}

@-webkit-keyframes introtext {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes introtext {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.iiw-f1 {
  font-size: 2vh;
  text-align: left;
  padding-left: 35vw;
  font-weight: bold;
  color: #615b75;
}

.iiw-f2 {
  font-size: 6vh;
  font-weight: bold;
  -webkit-text-fill-color: #615b75;
  -webkit-text-stroke-width: 3px;
  -webkit-text-stroke-color: #615b75;
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
  -webkit-animation-name: introtext;
          animation-name: introtext;
  -webkit-animation-duration: 3s;
          animation-duration: 3s;
}

.iiw-f2 label {
  -webkit-text-fill-color: #fd8f4c;
  -webkit-text-stroke-color: #fd8f4c;
  font-size: 6.5vh;
}
.iiw-f3 {
  font-size: 3.3vh;
  text-align: right;
  padding-right: 30vw;
  font-weight: bold;
  color: #615b75;
}
.iiw-f3 label {
  color: #facc08;
}

